import React,{useState} from 'react';
import axios from 'axios';
import {Table,Row, Col, Button} from 'react-bootstrap'
import ReactDOM from 'react-dom';


export default class PersonList extends React.Component {
    state = {
        persons: [],
        number: 0,
    }



    updateList(){
        this.state.number = 0;
        axios.get("http://localhost:8080/persons")
            .then(res => {
                const persons = res.data;
                console.log(persons);
                this.setState({ persons });
            })
    }

    removePerson(id){
        this.state.number = 0;
        console.log("handler");
        axios.delete("http://localhost:8080/persons/"+id,{data: {id: id}})
            .then(res => {
                console.log(res);
                console.log(res.data);
                this.updateList();
            });
    }

    componentDidMount() {
        this.state.number = 0;
        axios.get("http://localhost:8080/persons")
            .then(res => {
                const persons = res.data;
                console.log(persons);
                this.setState({ persons });
            })
    }

    render() {

        return (
            <Row>
                <Col>
                    <Table className='striped bordered hover'>
                        <thead>
                        <tr>
                            <th>#</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>age</th>
                            <th>  </th>
                        </tr>
                        </thead>
                        <tbody>
                        {
                            this.state.persons
                                .map(person =>
                                    <tr key={person.id}>
                                        <td>{++this.state.number}</td>
                                        <td>{person.fname}</td>
                                        <td>{person.lname}</td>
                                        <td>{person.age}</td>
                                        <td>
                                            <Button variant="danger" type="submit" onClick={this.removePerson.bind(this, person.id)}>
                                                Delete person
                                            </Button>
                                        </td>
                                    </tr>
                                )
                        }
                        </tbody>
                    </Table>
                </Col>
            </Row>
        )
    }
}